*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
:root {
    --first-color: #191e46;
    --second-color: #bed1d7;
    --third-color: #483551;
    --fourth-color: #fde8a9;
    --fifth-color: #f39b76;

}
body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    line-height: 1.6;
}
ul {
    list-style: none;
}
a {
    display: flex;
    text-decoration: none;
}
h1,
h2 {
    font-weight: 300;
    line-height: 1.2;
    margin: 10px 0;
}
p {
    margin: 10px 0;
}
img {
    width: 75%;
    max-width: 1200px;
    border-radius: 20px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); 
}


/* Project Sections */
.project a {
    color: var(--first-color);
    font-size: 28px;
    text-align: center;
    align-items: center;
    justify-content: center;
}
.project {
    background-color: var(--second-color);
    align-items: center;
    justify-content: center;
    text-align: center;
}
.project-2 {
    align-items: center;
    justify-content: center;
    text-align: center;
    max-width: 50%;
}
.project-2 a {
    color: var(--first-color);
    font-size: 28px;
    text-align: center;
    align-items: center;
    justify-content: center;
}
.container-3 {
    background-color: var(--fifth-color);
    width: 100%;
    color: var(--first-color);
    display: flex;
    flex-wrap: wrap;
}

body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    line-height: 1.6;
}
ul {
    list-style: none;
}
a {
    display: flex;
    text-decoration: none;
    color: var(--fifth-color);
}
h1,
h2 {
    font-weight: 300;
    line-height: 1.2;
    margin: 10px 0;
}
p {
    margin: 10px 0;
}
img {
    width: 75%;
    max-width: 1200px;
}

/*Hero Section*/
.showcase {
    height: 400px;
    background-color: var(--third-color);
    position: relative;
}
.showcase h1 {
    font-size: 40px;
}
.showcase p {
    margin: 20px 0;
}

/*My languages */
.languages {
    padding: 50px;
    position: relative;
    display: flex;
    align-items: flex-start;
    margin: auto;
    height: 550px;
}
.languages h2 {
    font-size: 48px;
    border-bottom: 3px solid ;
    color: var(--fourth-color);
    padding-bottom: 20px;
    text-align: center;
    margin: 0 auto 35px auto;
    width: 50%;
    overflow: visible;
}
.languages .card {
    margin-top: 60px;
    height: 50%;
    text-align: center;
    margin: 18px 10px 40px;
    transition: transform 0.2s ease-in;
}
.languages .card .code {
    height: 50px;
}
.languages .card h4 {
    color: var(--fifth-color);
    font-size: 20px;
    margin-bottom: 10px;
}
.languages .card:hover {
    transform: translateY( -15px);
}
.background {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.background img {
    border-radius: 15px;
    margin: 30px;
    box-shadow: 0 4px 10px rgba(255, 255, 255, 0.2); 
}
.title {
    font-size: 30px;
    font-weight: bold;
    padding: 40px 100px;
}

.showcase-form-2 {
    position: relative;
    height: 300px;
    width: 250px;
    z-index: 100px;
    justify-self: flex-end;
    background-color: var(--fifth-color);
}
.showcase-form .form-control {
    margin: 10px 0;
}
.showcase-form input [type='text'],
.showcase-form input [type='email']{ 
    border: 0;
    border-bottom: 1px solid var(--second-color);
    width: 100%;
    font-size: 16px;
    padding: 3px;
}
.showcase-form-2 input {
    outline: none;
}
body {
    -webkit-user-select: none; 
    -moz-user-select: none;    
    -ms-user-select: none;   
    -o-user-select: none;
    user-select: none;
}
/* Utilities*/
.container {
    max-width: 1400px;
    margin: 0 auto;
    overflow: auto;
    padding: 0 40px;
}
.container-2 {
    margin: 0 auto;
    overflow: auto;
    padding: 0 40px;
    border-radius: 50px;
    max-width: 90%;
    border-bottom: 20px solid var(--fifth-color);
}
.flex {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}
.flex .between {
    justify-content: space-between;
}
.grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    justify-content: center;
    align-items: center;
    height: 100%;
}
.card {
    background-color: var(--second-color);
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(255, 255, 255, 0.2); 
    padding: 20px;
    margin: 10px;
    color: var(--third-color);
}
.btn {
    display: inline-block;
    padding: 10px 30px;
    cursor: pointer;
    background-color: var(--third-color);
    color: var(--second-color);
    border: none;
    border-radius: 5px
}
.btn-outline {
    background-color: transparent;
    border: 1px solid var(--second-color)
}
.btn:hover {
    transform: scale(0.98);
}
/*Margin*/
.my-1 {
    margin: 1rem 0;
}
.my-2 {
    margin: 1.5rem 0;
}
.my-3 {
    margin: 2rem 0;
}
.my-4 {
    margin: 3rem 0;
}
.my-5 {
    margin: 4rem 0;
}
.m-1 {
