}

/*2:1 grid section*/
.grid-stuff {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
}
.grid-stuff >*:first-child{
    grid-column: 1 / span 2;
    grid-row: 1 / span 2;
}
.bg-primary,
.btn-primary {
    background-color: var(--first-color);
    color: var(--fifth-color);
}

.bg-secondary,
.btn-secondary {
    background-color: var(--third-color);
    color: var(--second-color);
}
.bg-light,
.btn-light {
    background-color: var(--fifth-color);
    color: var(--first-color);
}
.bg-dark,
.btn-dark {
    background-color: var(--first-color);
    color: var(--fourth-color);
}
.btn-light-2 {
    background-color: var(--fourth-color);
    color: var(--third-color);
    font-weight: bold;
}

/* Text sizes */
.lead {
    font-size: 20px;
} 
.sm {
    font-size: 1rem;
}

.md {
    font-size: 2rem;
}

.lg {
    font-size: 3rem;
}

.xl {
    font-size: 4rem;
}

.text-center {
    text-align: center;
}
.text-center {
    text-align: left;
}
/* Footer Styles */
footer {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--first-color);
    color: var(--second-color);
    padding: 20px;
}
.pos-1 {
    background-color: var(--first-color);
    order: 2;
}
footer a {
    display: flex;
    justify-content: space-around;
    color: var(--second-color);
    font-weight: bold;
    font-size: 18px;
}
footer nav{
    order: 1;
    display: flex;
    justify-content: space-between;
}
.pos-1 img {
    max-width: 200px;
    border-radius: 100px;
}
footer a{
    padding: 20px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.pos-3 {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    order: 3;
}
footer nav ul {
    padding: 20px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.pos-1 img {
    max-width: 200px;
    border-radius: 100px;
    margin: 10px;
}

/* Header Styling */
header {
    background-color: var(--first-color);
    padding: 20px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
header h1 {
    margin: 0;
    font-size: 40px;
    color: var(--second-color);
    height: 70px;
}

.navbar ul {
    display: flex;
}
header a {
    text-decoration: none;
    color: var(--fifth-color);
    padding: 10px;
    margin: 0 5px;
    font-size: 30px;
}
header nav ul li a:hover {
    background: var(--fourth-color);
    color: var(--first-color);
    border-radius: 15px;
    text-shadow: none;
}
footer a:hover {
    background: var(--fourth-color);
    color: var(--first-color);
    border-radius: 15px;
    text-shadow: none;
}
/* Screen Sizes */

/* this will be applied on any screen smaller then 980px */
@media screen and (max-width: 980px) {
    header {
        height: 110px;
        flex-direction: column;
    }
    header nav ul {
        padding: 10px;
        background-color: rgba(255, 255, 255, 0.1);
    }
    .flex {
        flex-direction: column;
    }
    .languages {
        height: auto;
        width: auto;
    }
    #form {
        order: 2;
    }
    #languages {
        order: 1;
    }
    .pos-1 img {
        padding: 10px;
        max-width: 200px;
        border-radius: 100px;
        height: auto;
        width: auto;    
    }
    .pos-2 {
        flex-direction: column;
        height: auto;
        width: auto;
    }
    .pos-3 {
        flex-direction: column;
        height: auto;
        width: auto;
    }
    img {
        max-width: 50%;
    }
    .project {
        align-items: center;
        justify-content: center;
        text-align: center;
    }
    .project a {
        text-align: center;
        align-items: center;
        justify-content: center;
    }
}

